/**
 * Verify 验证码组件
 * @description 分发验证码使用
 * */
import VerifySlide from './verifySlider/verifySlider'
import VerifyPoint from "./verifyPoint/verifyPoint"

export default {
    name: 'Vue2Verify',
    props: {
        captchaType:{
            type:String,
            required:true
        },
        figure: {
            type: Number
        },
        arith: {
            type: Number
        },
        mode: {
            type: String,
            default:'pop'
        },
        vSpace: {
            type: Number,
            default:5
        },
        explain: {
            type: String,
            default: '向右滑动完成验证'
        },
        imgSize: {
            type: Object,
            default() {
                return {
                    width: '310px',
                    height: '155px'
                }
            }
        },
        blockSize: {
            type: Object,
            default() {
                return {
                    width: '50px',
                    height: '50px'
                }
            }
        },
        barSize: {
            type: Object
        },
    },
    data() {
        return {
            // showBox:true,
            clickShow:false,
            // 内部类型
            verifyType: undefined,
            // 所用组件类型
            componentType: undefined
        }
    },
    methods: {
        /**
         * refresh
         * @description 刷新
         * */
        refresh() {
            if (this.instance.refresh) {
                this.instance.refresh()
            }
        },
        show(){
            if (this.mode=="pop") {
                this.clickShow = true;
            }
        }
    },
    computed: {
        instance() {
            return this.$refs.instance || {}
        },
        showBox(){
            if (this.mode=='pop') {
                return this.clickShow
            }else{
                return true;
            }
        }
    },
    watch: {
        captchaType:{
            immediate: true,
            handler(captchaType) {
                switch (captchaType.toString()) {
                    case 'blockPuzzle':
                        this.verifyType = '2'
                        this.componentType = 'VerifySlide'
                        break
                    case 'clickWord':
                        this.verifyType = ''
                        this.componentType = 'VerifyPoints'
                        break
                }
            }
        },
    },
    components: {
        VerifySlide,
        VerifyPoint
    },
}
